<template>
  <section :class="calcWrapClass">
    <div class="modal-window__content">
      <h4 class="modal-window__content-title">–í—ã –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å –∫–Ω–∏–≥—É?</h4>
      <div class="modal-window__content-function">
        <button class="btn" @click="removeBook">–¥–∞</button>
        <button class="btn" @click="$parent.modalWindowOpen = false">–Ω–µ—Ç</button>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: { value: { type: Boolean } },
  computed: {
    calcWrapClass() {
